import { Suspense } from "react"
import { getBooks } from "@/lib/baserow/client"
import { BookAgGrid } from "@/components/book-ag-grid"
import { Skeleton } from "@/components/ui/skeleton"

export default async function BooksPage() {
  return (
    <div className="container mx-auto px-4 py-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Browse Books</h1>
        <p className="text-muted-foreground">Search and filter our collection using the grid below</p>
      </div>

      <Suspense fallback={<BookGridSkeleton />}>
        <BookList />
      </Suspense>
    </div>
  )
}

async function BookList() {
  try {
    const books = await getBooks()

    if (!books || books.length === 0) {
      return (
        <div className="text-center py-12">
          <p className="text-muted-foreground">No books found.</p>
        </div>
      )
    }

    return <BookAgGrid books={books} />
  } catch (error) {
    console.error("Error loading books:", error)
    return (
      <div className="text-center py-12">
        <p className="text-muted-foreground">Error loading books. Please try again.</p>
      </div>
    )
  }
}

function BookGridSkeleton() {
  return (
    <div className="mt-8">
      <Skeleton className="h-[600px] w-full rounded-lg" />
    </div>
  )
}

